import React, { FunctionComponent, useEffect, useRef, useState } from 'react'
import classnames from 'classnames'

const Styles = require('./audioPlayer.module.styl')

type Props = {
  sources: string[]
}

export const AudioPlay: FunctionComponent<Props> = props => {
  const canvasRef = useRef<HTMLCanvasElement>(null)
  const [dataArray, setDataArray] = useState<Uint8Array>(new Uint8Array(75))
  const [permission, setPermission] = useState(false) // 用户授权播放音乐
  const [paused, setPaused] = useState(true)
  const [currentTime, setCurrentTime] = useState(0)
  const operatorRef = useRef<HTMLDivElement>()
  const audioContainerRef = useRef<HTMLDivElement>()
  const audioRef = useRef<HTMLAudioElement>(null)
  const animationRef = useRef<number>()
  const audioDateRef = useRef<Uint8Array>(new Uint8Array(75))
  const analyserRef = useRef<AnalyserNode>()
  // @ts-ignore
  const context = useRef<AudioContext>()

  useEffect(() => {
    if (!operatorRef.current || !audioContainerRef.current || !audioRef.current) return
    operatorRef.current.style.transform = `translateX(${(currentTime / audioRef.current.duration) *
      audioContainerRef.current.offsetWidth}px) translateY(-50%)`
  }, [operatorRef.current, currentTime, audioContainerRef.current, audioRef.current])

  useEffect(() => {
    if (!permission) return
    const source = context.current.createMediaElementSource(audioRef.current)
    analyserRef.current = context.current.createAnalyser()
    source.connect(analyserRef.current)
    analyserRef.current.connect(context.current.destination)

    return () => {
      source.disconnect()
      analyserRef.current.disconnect()
      cancelAnimationFrame(animationRef.current)
    }
  }, [permission])

  useEffect(() => {
    if (!permission) return
    animationRef.current = requestAnimationFrame(tick)
    return () => cancelAnimationFrame(animationRef.current)
  }, [permission])

  useEffect(() => {
    if (!dataArray) return
    const cxt = canvasRef.current.getContext('2d')
    const canvas = canvasRef.current
    cxt.clearRect(0, 0, canvas.width, canvas.height)
    //画线条
    for (var i = 0; i < 75; i++) {
      cxt.beginPath()
      cxt.lineWidth = 2
      cxt.moveTo(i * 4, 75)
      cxt.lineTo(i * 4, 75 - (dataArray[i] / 256) * (dataArray[i] / 256) * 30)
      cxt.stroke()

      cxt.beginPath()
      cxt.lineWidth = 2
      cxt.moveTo(i * 4, 75)
      cxt.lineTo(i * 4, 75 + (dataArray[i] / 256) * (dataArray[i] / 256) * 30)
      cxt.stroke()
    }
  }, [dataArray, canvasRef.current])

  const tick = () => {
    animationRef.current = requestAnimationFrame(tick)
    analyserRef.current.getByteFrequencyData(audioDateRef.current)
    setDataArray(audioDateRef.current.slice(0))
  }

  const handlePlayMusic = () => {
    // @ts-ignore
    context.current = new (window.AudioContext || window.webkitAudioContext)()
    if (typeof context.current.resume === 'function') {
      context.current.resume()
    }
    setPermission(true)
    audioRef.current.play()
    setPaused(false)
  }

  const handlePauseMusic = () => {
    audioRef.current.pause()
    setPaused(true)
  }

  return (
    <div className={Styles['container']}>
      <audio
        onTimeUpdate={() => setCurrentTime(audioRef.current.currentTime)}
        loop={true}
        className={Styles['audio']}
        ref={audioRef}
        controls={true}
        preload='metadata'
      >
        {props.sources.map(source => (
          <source key='source' src={source} />
        ))}
      </audio>
      <div ref={audioContainerRef} className={Styles['audioPlayer']}>
        <div ref={operatorRef} className={Styles['operator']}>
          {paused ? (
            <i onClick={handlePlayMusic} className={classnames(Styles['playIcon'], Styles['icon'], 'iconfont')}>
              &#xe699;
            </i>
          ) : (
            <i onClick={handlePauseMusic} className={classnames(Styles['pauseIcon'], Styles['icon'], 'iconfont')}>
              &#xe650;
            </i>
          )}
        </div>
        <div className={classnames(Styles['info'], { [Styles['hidden']]: !paused })}>Love - S.E.N.S</div>
        <canvas className={classnames(Styles['canvas'], { [Styles['hidden']]: paused })} ref={canvasRef} />
      </div>
    </div>
  )
}
