import React, { FunctionComponent } from 'react'
import { graphql, Link } from 'gatsby'
import Layout from '../components/layout/layout'
import SEO from '../components/seo'
import { Site } from '../graphql-types'
import { AudioPlay, AudioWrap } from '../components/AudioPlayer/'
import { animated, useSpring } from 'react-spring'

const Styles = require('../styles/pages/index.module.styl')

type Props = {
  data: {
    site: Site
  }
  location: Location
}

const Me: FunctionComponent<Props> = props => {
  const { data } = props

  const spring = useSpring({ opacity: 1, transform: 'translate3d(0,0,0)', from: { opacity: 0, transform: 'translate3d(0,-30%,0)' } })

  return (
    <div>
      <SEO title="it's me" />
      <animated.div style={spring} className={Styles['content']}>
        <div className={Styles['avatar']} />
        <div className={Styles['name']}>Haoqin Zhang</div>
        <div className={Styles['line']} />
        <div className={Styles['info']}>Coding Lover</div>
        <div className={Styles['loveWord']}>痛苦是财富，这话是扯淡。痛苦就是痛苦，对痛苦的思考才是财富</div>
        <div className={Styles['music']}>
          <AudioWrap>
            <AudioPlay sources={['../music/me.mp3']} />
          </AudioWrap>
        </div>
        <Link className={Styles['enter']} to='/posts'>
          Enter
        </Link>
      </animated.div>
    </div>
  )
}

export default Me

export const pageQuery = graphql`
  query {
    site {
      siteMetadata {
        title
      }
    }
  }
`
